@font-face {
  font-family: 'Judson';
  src: url('./font/Judson-Regular.ttf') format('truetype'); /* 실제 폰트 파일의 경로를 업데이트하세요 */
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Judson Bold';
  src: url('./font/Judson-Bold.ttf') format('truetype'); /* 실제 폰트 파일의 경로를 업데이트하세요 */
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}


a:visited {
  color: #929292;
}

a:link {
  color: #000;
}

* {
  font-family: 'Pretendard-Regular';
  font-size: 13px;
}

body {
}

li {
}

ul {
  list-style-type: square;
  padding-top: 20px;
  margin-block-start: 0;
  padding-inline-start: 20px;
}

.second-ul {
  padding-top: 5px;
  padding-left: 5px;
}

.second-li { 
  list-style-type: none;
  font-size: 12px;
  color: #7f7f7f;
  padding-inline-start: 5px;
  margin-top: 5px;
}

.strong {
  font-weight: bold;
  font-size: 14px;
}

.Header {
  position: fixed; /* 고정 위치 */
  top: 0; /* 화면 상단에 고정 */
  left: 0; /* 화면 좌측에 고정 */
  right: 0; /* 화면 우측에 고정 */
  z-index: 100; /* 다른 요소 위에 표시 */
  background-color: white;
  /* border: 0.2px solid black; */
  width: 100vw;
  height: 200px;
  min-width: 858px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Header_content {
  padding: 0 28.5px 0 28.5px;
  max-width: 858px;
  width: 858px; /* 가로 크기 858px로 설정 */
  height: 155px; /* 세로 크기 155px로 설정 */
  position: absolute; /* 절대 위치 지정 */
  top: 50%; /* 상단에서 50% 위치로 이동 */
  left: 50%; /* 좌측에서 50% 위치로 이동 */
  transform: translate(-50%, -50%); /* 중앙 정렬 */
  display: flex;
}

#header-left {
  width: 67%;
}

#header-right {
  border-left: 1.802px solid rgba(108, 108, 108, 0.70);
  width: 33%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#name {
  font-family: 'Judson Bold';
  font-size: 82px;
  color: #5D7383;
  margin-bottom: 14px;
}

#job {
  color: #6C6C6C;
  font-family: 'Pretendard-Regular';
  font-size: 27.025px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 4.054px;
  margin-left: 1px;
}

.info-icon {
  width: 25px;
  margin-right: 12px;
}

.info-div {
  font-family: 'Pretendard-Regular';
  font-size: 14px;
  font-weight: 500;
  margin-left: 37px;
  display: flex;
  align-items: center;
}

.info-mg {
  margin: 14px 0 0 37px;
}

.App {
  width: 100vw;
  height: 2600px;
  white-space: nowrap;
  margin-bottom: 40px;
}

.main-container {
  margin: auto;
  margin-top: 214px;
  width: 908px;

  background-color: white;
  display: flex;
}

.main-left {
  width: 312px;
  /* border: 1px solid black; */
  height: 100vh;
  margin-right: 56px;
}

.main-right {
  width: 540px;
  height: 2900px;
