  width: 95vw;
}

.script-title-sq {
  width: 25.224px;
height: 25.224px;
background: rgba(90, 119, 140, 0.20);
position: absolute;
top: 0;
left: 0;
}

.script-title-txt {
  color: #000;
  font-family: 'Judson Bold';
  font-size: 30.629px;
  position: absolute;
  left: 15px;
  display: block;
}

.script-txt {
  display: block;
  white-space:normal;
